<template>
  <div class="cr">
    <h1>{{id ? '修改':'新建'}}笔记</h1>
    <el-form :label-position="labelPosition" :model="html" @submit.native.prevent="sava">
      <el-form-item label="大标题">
        <el-input v-model="html.bigtitle"></el-input>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="html.title"></el-input>
      </el-form-item>

      <el-form-item label="名称">
        <el-input v-model="html.name"></el-input>
      </el-form-item>
      <el-form-item label="所属分类">
        <el-select v-model="html.parent">
          <el-option v-for="item in parents" :key="item._id" :label="item.title" :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <vue-editor v-model="html.say"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { VueEditor } from "vue2-editor";
export default {
  props: {
    id: {}
  },
  data() {
    return {
      labelPosition: "right",
      html: {
        name: "",
        title: "",
        say: "",
        parents: ""
      },
      parents: []
    };
  },
  components: {
    VueEditor
  },
  methods: {
    async sava() {
      let res;
      if (this.id) {
        res = await this.$http.put(`rest/html/create/${this.id}`, this.html);
      } else {
        res = await this.$http.post("rest/html/create", this.html);
      }
      this.$router.push("/html/list");
      this.$message({
        type: "success",
        message: "保存成功"
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/html/create/${this.id}`);
      this.html = res.data;
    },
    async fetchop() {
      const res = await this.$http.get("rest/html");
      this.parents = res.data;
    }
  },
  created() {
    this.fetchop();
    if (this.id) {
      this.fetch();
    }
  }
};
</script>
<style>
.cr {
  width: 70%;
  margin: 40px auto;
}
</style>