<template>
<div class="bg">
<div class="login">
    <h1>登陆界面</h1>
  <el-form @submit.native.prevent="login">
    <el-form-item label="账号"><i class="el-icon-user"></i>
      <el-input v-model="model.username"></el-input>
    </el-form-item>
    <el-form-item label="密码">
      <el-input type="password" v-model="model.password"></el-input>
    </el-form-item>
    <el-form-item>
        <el-button type="info" native-type="submit">登陆</el-button>
    </el-form-item>
  </el-form>
</div></div>
</template>
<style>
.login{
    max-width: 400px;
    width: 50%;
    margin:100px auto;
}
</style>
<script>
export default {
  data(){
    return{
      model:{
        username:''
      }
    }
  },
  methods:{
    async login(){
     const res = await this.$http.post('adminuser',this.model);
     sessionStorage.token = res.data.token;
     this.$router.push('/');
     this.$message({
       type:'success',
       message:'登陆成功'
     })
    }
  }
}
</script>