 <template>
  <div>
    <h1>笔记列表</h1>
    <el-table :data="data" style="width: 100%">
      <el-table-column prop="bigtitle" label="大标题bigtitle"></el-table-column>
      <el-table-column prop="title" label="标题title"></el-table-column>
      <el-table-column prop="name" label="名称name"></el-table-column>
      <el-table-column prop="parent.title" label="上级主题"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="$router.push(`/js/create/${scope.row._id}`)">编辑</el-button>
          <el-button size="mini" type="danger" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: null
    };
  },
  created() {
    this.fetch();
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/js");
      this.data = res.data;
    },
    async remove(item) {
      this.$confirm("此操作将永久删除该内容, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async () => {
        await this.$http.delete(`rest/js/${item._id}`);
        this.$message({
          type: "success",
          message: "删除成功!"
        });
        this.fetch();
      });
    }
  }
};
</script>
