<template>
  <div class="cr">
    <h1>{{id ? '修改':'新建'}}笔记</h1>
    <el-form :label-position="labelPosition"  :model="js" @submit.native.prevent="sava">
      <el-form-item label="大标题">
        <el-input v-model="js.bigtitle"></el-input>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="js.title"></el-input>
      </el-form-item>
      <el-form-item label="名称">
        <el-input v-model="js.name"></el-input>
      </el-form-item>
      <el-form-item label="所属分类">
        <el-select v-model="js.parent">
          <el-option v-for="item in parents" :key=item._id 
          :label="item.title" :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <vue-editor v-model="js.say"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { VueEditor } from "vue2-editor";
export default {
  props:{
    id:{}
    
  },
  data() {
    return {
      labelPosition: "right",
      js: {
        name: "",
        title: "",
        say: "",
        parents:"",
        bigtitle:""
        
      },
      parents:[],
      parents1:[],

    };
  },
   components: {
    VueEditor
  },
  methods:{
     async sava(){
      
       if(this.id){
          await this.$http.put(`rest/js/create/${this.id}`,this.js)
       }else{
         await this.$http.post('rest/js/create',this.js)
       }
       this.$router.push('/js/list')
          this.$message({
            type:'success',
            message:'保存成功'
          });
     },
     async fetch(){
       const res = await this.$http.get(`rest/js/create/${this.id}`)
       this.js = res.data
     },
      async fetchop(){
       const res = await this.$http.get("rest/js")
       this.parents = res.data
     }
  },
  created(){
    this.fetchop()
    if(this.id){
      this.fetch()
    }
    
  }
};
</script>
<style>
.cr {
  width: 70%;
  margin: 40px auto;
}
</style>