import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import jscreate from './views/jscreate.vue'
import jslist from './views/jslist.vue'
import login from './views/login.vue'
import logincreate from './views/logincreate.vue'
import loginlist from './views/loginlist.vue'
import htmlcreate from './views/htmlcreate.vue'
import htmllist from './views/htmllist.vue'
import csscreate from './views/csscreate.vue'
import csslist from './views/csslist.vue'
import nodecreate from './views/nodecreate.vue'
import nodelist from './views/nodelist.vue'
import mongodbcreate from './views/mongodbcreate.vue'
import mongodblist from './views/mongodblist.vue'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children:[
        {path:'/js/create',component:jscreate},
        {path:'/js/create/:id',component:jscreate,props:true},
        {path:'/js/list',component:jslist},

        {path:'/html/create',component:htmlcreate},
        {path:'/html/create/:id',component:htmlcreate,props:true},
        {path:'/html/list',component:htmllist},

        {path:'/css/create',component:csscreate},
        {path:'/css/create/:id',component:csscreate,props:true},
        {path:'/css/list',component:csslist},

        {path:'/node/create',component:nodecreate},
        {path:'/node/create/:id',component:nodecreate,props:true},
        {path:'/node/list',component:nodelist},

        {path:'/mongodb/create',component:mongodbcreate},
        {path:'/mongodb/create/:id',component:mongodbcreate,props:true},
        {path:'/mongodb/list',component:mongodblist},
        
        {path:'/logincreate',component:logincreate},
        {path:'/logincreate/:id',component:logincreate,props:true},
        {path:'/loginlist',component:loginlist },
      ]
    },
    {
      path:'/login',component:login,meta:{isPublic:true}
    }
  ]
})
router.beforeEach((to,from,next)=>{
  if(!to.meta.isPublic && !sessionStorage.token){
    return next('/login')
  }
  next()
})
export default  router
