<template>
  <div class="cr">
    <h1>{{id ? '修改':'新建'}}用户</h1>
    <el-form :label-position="labelPosition"  :model="js" @submit.native.prevent="sava">
      <el-form-item label="名称">
        <el-input v-model="js.name"></el-input>
      </el-form-item>
      <el-form-item label="用户名">
        <el-input v-model="js.username"></el-input>
      </el-form-item>
       <el-form-item label="密码">
         <el-input type="password" v-model="js.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props:{
    id:{}
    
  },
  data() {
    return {
      labelPosition: "right",
      js: {
        name: "",
        username: "",
        password: "",
      },
      parents:[]
    };
  },
  methods:{
     async sava(){
       if(this.id){
       await this.$http.put(`/rest/adminuser/create/${this.id}`,this.js)
       }else{
       await this.$http.post('/rest/adminuser/create',this.js)
       }
       this.$router.push('/loginlist')
          this.$message({
            type:'success',
            message:'保存成功'
          });
     },
     async fetch(){
       const res = await this.$http.get(`/rest/adminuser/create/${this.id}`)
       this.js = res.data
     },
      async fetchop(){
       const res = await this.$http.get("/rest/adminuser")
       this.parents = res.data
     }
  },
  created(){
    this.fetchop()
    if(this.id){
      this.fetch()
    }
    
  }
};
</script>
<style>
.cr {
  width: 70%;
  margin: 40px auto;
}
</style>